.Footer {
  padding-top: 50vh;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.footerItem {
  font-weight: bold;
  list-style: none;
  display: inline-block;
  padding-left: 0.8rem;
  font-size: 1.5rem;
  opacity: 0.7;
  transition: opacity 0.8s;
}

.footerItem > .icons {
  color: #1f1f1f;
}

.footerItem:hover {
  opacity: 1;
}