@font-face {
  font-family: 'SK-Modernist';
  src: url('/src/fonts/Sk-Modernist-Regular.otf');
}

* {
  box-sizing: border-box;
  margin: 0;
  padding:0;
}

html,
body {
   width:100%;
   height:100%;
   font-size: 100%;
}

body {
  background-color: #f3f3f3;
  font-family: 'Helvetica Neue', 'Roboto',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  color:rgb(29, 29, 29);
  text-decoration: none;
  display: inline-block;
  position: relative;
}

a:after {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 1px;
  bottom: 0;
  left: 0;
  background-color: #1d1d1d;
  transform-origin: bottom right;
  transition: transform 0.3s ease-out;
}

a:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

h1 {
  font-family: 'SK-Modernist';
  font-size: 4rem;
  letter-spacing: -0.02em;
}

.container {
  margin-left: 2vw;
  margin-right: 2vw;
}

.container-route {
  padding-top: 20vh;
  padding-bottom: 5vh;
  /* text-transform: uppercase; */
}

.container-app {
  padding-top: 5vh;
  padding-bottom: 7vh;
  /* background-color: rgb(190, 190, 190); */
}

.Fade-in {
  animation: fade-in 1s !important;
}

.Fade-in-b {
  animation: fade-in 1.5s !important;
}

@keyframes fade-in {
  from {
    opacity: 0%;
  }
  to {
    opacity: 100%;
  }
}