.Project-Grid {
  display: grid;
  margin-top: 2.5vh;
  margin-bottom: 2vh;
  letter-spacing: 0.01em;
  align-items: center;
  color:#1d1d1d;
  align-self: center;
  align-items: start;
  align-content: start;
  grid-auto-columns: 1fr;
  grid-column-gap: 2vw;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.Featured-Project {
  position: relative;
  /* background-color: blueviolet; */
}

.Project-img {
  width: 100%;
  height: 35vw;
  object-fit: cover;
  opacity: 0.8;
  transition: opacity .8s ease-in-out, transform .4s ease-in-out;
}

.Project-img:hover {
  transform: translateY(-5px); 
  opacity: 1;
}

.Centered-Text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -450%);
  z-index: 1;
}

.Text-Stack {
  padding-top: 0.5rem;
  font-size: 0.6rem;
}

.Text-Title {
  padding-top: 0.4rem;
  padding-bottom: 5rem;
  font-size: 1.2rem;
}